* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overscroll-behavior: none;
}

.app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.main-content {
  flex: 1;
  padding-bottom: 60px; /* Space for bottom navigation */
}

/* Apply theme colors automatically via CSS variables */
body {
  --primary: #4A8FE7;
  --primaryLight: #EBF3FE;
  --primaryDark: #3A7DD1;
  --secondary: #8A6DE9;
  --accent: #FF9A3C;
  --accentDark: #F27D00;
  --success: #4CAF50;
  --error: #F44336;
  --info: #2196F3;
  
  --text: #000000; /* Pure black for better contrast */
  --textLight: #444444;
  --textInverse: #FFFFFF;
  
  --background: #FFFFFF; /* Pure white background */
  --backgroundLight: #FFFFFF; /* Pure white for light areas */
  --backgroundDark: #F5F7FA; /* Light gray for "darker" areas */
  --backgroundInput: #FFFFFF;
  
  --border: #CCCCCC;
  --buttonText: #FFFFFF;
  
  background-color: var(--background);
  color: var(--text);
}

/* Dark theme detection */
@media (prefers-color-scheme: dark) {
  body:not(.light-theme) {
    --primary: #5C9CE6;
    --primaryLight: #243952;
    --primaryDark: #3A7DD1;
    --secondary: #9B7FF0;
    --accent: #FFA94D;
    --accentDark: #F27D00;
    --success: #66BB6A;
    --error: #EF5350;
    --info: #42A5F5;
    
    --text: #E0E0E0;
    --textLight: #B0B0B0;
    --textInverse: #333333;
    
    --background: #121212;
    --backgroundLight: #1E1E1E;
    --backgroundDark: #2D2D2D;
    --backgroundInput: #2D2D2D;
    
    --border: #555555;
    --buttonText: #FFFFFF;
  }
}

/* Theme toggling via class */
body.dark-theme {
  --primary: #5C9CE6;
  --primaryLight: #243952;
  --primaryDark: #3A7DD1;
  --secondary: #9B7FF0;
  --accent: #FFA94D;
  --accentDark: #F27D00;
  --success: #66BB6A;
  --error: #EF5350;
  --info: #42A5F5;
  
  --text: #E0E0E0;
  --textLight: #B0B0B0;
  --textInverse: #333333;
  
  --background: #121212;
  --backgroundLight: #1E1E1E;
  --backgroundDark: #2D2D2D;
  --backgroundInput: #2D2D2D;
  
  --border: #555555;
  --buttonText: #FFFFFF;
}

/* Apply light theme manually when needed */
body.light-theme {
  --primary: #4A8FE7;
  --primaryLight: #EBF3FE;
  --primaryDark: #3A7DD1;
  --secondary: #8A6DE9;
  --accent: #FF9A3C;
  --accentDark: #F27D00;
  --success: #4CAF50;
  --error: #F44336;
  --info: #2196F3;
  
  --text: #000000; /* Pure black for better contrast */
  --textLight: #444444;
  --textInverse: #FFFFFF;
  
  --background: #FFFFFF; /* Pure white background */
  --backgroundLight: #FFFFFF; /* Pure white for light areas */
  --backgroundDark: #F5F7FA; /* Light gray for "darker" areas */
  --backgroundInput: #FFFFFF;
  
  --border: #CCCCCC;
  --buttonText: #FFFFFF;
}

/* Accessibility and usability improvements */
button:focus, input:focus, select:focus, textarea:focus {
  outline: 2px solid var(--primary);
  outline-offset: 2px;
}

a {
  color: var(--primary);
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

/* Prevent overflow issues */
img, video {
  max-width: 100%;
  height: auto;
}

/* Disable pull-to-refresh on mobile */
html {
  overscroll-behavior-y: contain;
}

/* Improved input styling */
input, select, textarea, button {
  color: var(--text); /* Ensure form controls use the text color */
  background-color: var(--backgroundInput);
}

option {
  background-color: var(--backgroundInput);
  color: var(--text); /* Ensure dropdown options use the text color */
}
